.grid-container {
  display: grid;
  grid-template-areas:
  'header'
  'main'
  'footer';
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}
header {
  grid-area: header;
  background-color:rgb(4, 155, 62);
  text-decoration-color: peru;
}
main {
  grid-area: main;
  padding: 10rem;
  margin: 10rem;
}
footer{
  grid-area: footer;
  background-color:rgb(4, 155, 62);
  color: white;
}

a {
  text-decoration: none;
}
a:hover {
  color:tomato;
}
header a {
  color: white;
  padding: 1rem;
}
a.brand {
  color: white;
  font-size: 3rem;
  font-weight: bold;
}
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 10rem;
}
.row.center {
  justify-content: center;
}
.row.right {
  justify-content: right;
}
.row.left {
  justify-content: left;
}

.image {
  margin: 2rem;
}

image.resize {
  height: 15rem;
  width: 15rem;
}

center {
  justify-content: center;
}

.color {
  color:blue
}
.p1 {
  font-family: "Times New Roman", Times, serif;
}

.p2 {
  font-family: Arial, Helvetica, sans-serif;
}

.p3 {
  font-family: "Lucida Console", "Courier New", monospace;
}


