.row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 25px;
  }
  .row.center {
    justify-content: center;
    padding-right: 25px;
  }
  .row.right {
    justify-content: right;
  }
  .row.left {
    justify-content: left;
  }

  .body {
      display: flex;
      flex-wrap: wrap;
      justify-content: left;
      margin-left: 25px;
  }
  .body.center {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.photo {
  height: 250px;
  width: 500px;
}
.smallphoto {
  height: 200;
  width: 400;
}
.color {
  color:blue
}
