html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body {
  margin: 0;
  height: 100vh;
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  
}
#root {
  height: 100%;
}

.grid-container {
  display: grid;
  grid-template-areas:
  'header'
  'main'
  'footer';
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}
header {
  grid-area: header;
  background-color:paleturquoise;
}
main {
  grid-area: main;
  padding: 1rem;
}
footer{
  grid-area: footer;
  background-color: paleturquoise;
}

.image {
  height: 10rem;
  width: 10rem;
}
